#page {
	overflow: hidden;
	position: relative;
}

#primary {
	margin-bottom: 80px;
}

#social-wrapper {
	z-index: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-color: $darkgold;

	@media (max-width: 767px) {
		display: none;
	}
}

#color {
	position: absolute;
	left: 0;
	right: 0;
	height: 4000px;
	background: white;
	opacity: .93;

	@media (max-width: 767px) {
		display: none;
	}
}

#social-top {
    background: transparent;
    z-index: 10;
    position: relative;
	text-align: center;
	margin: auto;

	h1, h4, p { color: $purple; }

	h1 {
		display: inline-block;
		text-transform: uppercase;
		font-size: 72px;
		font-weight: 900;
		margin-top: 70px;

		@media (max-width: 768px) {
			font-size: 36px;
		}
	}

	h4 {
		font-family: "Encode Sans Compressed", sans-serif;
		font-weight: 800;
		font-size: 21px;
		line-height: 1.3;
		margin-top: 14.5px;
    	margin-bottom: 14.5px;
	}

    p {
        font-family: 'Encode Sans Compressed', sans-serif;
        font-size: 22px;
		font-weight: 600;
	}

	ul {
		margin-top: 20px;
		margin: 35px 0px 0px 0px;
		padding: 0px;

		li {
			display: inline;
			background: none;
			padding-left: 0;

			img {
				margin: 10px 20px 25px 20px;
			}
		}
	}

	@media (max-width: 767px) {
		h1 {
            margin: 0px;
            padding: 30px 0px 15px 0px;
		}

        p {
            padding: 20px;
            margin: 0;
		}

        ul {
            margin-top: 0px;
        }
	}
}

h2 {
	font-size: 55px;
	line-height: 60px;
	font-weight: 800;
	margin: 50px 0 10px;
}

#content {
	text-align: center;
	background: transparent;
	position: relative;
	z-index: 10;
	margin: 40px auto 40px auto;
	width: 90%;

    .feed {
		position: relative;
        background: transparent;
        max-height: 3500px;
        overflow: hidden;
        opacity: 1; //this can go back to 0 after JS is added
		transition: opacity .5s;

        &.show {
            opacity: 1;
		}

        h3 {
			font-family: 'Encode Sans Compressed', sans-serif;

            a, a:visited {
				color: $purple;

				&:hover { color: #888; }
            }
		}

        div {
            background-color: white;
            text-align: center;
            padding: 20px;
            margin-bottom: 30px;
            position: relative;
            border-top: 6px solid #b7a57a;
			box-shadow: 1px 1px 3px rgba(0,0,0,.1);

			a:hover, a:focus {
				text-decoration: none;
			}

			p {
				word-wrap: break-word;
				margin: 10px;
				color: #3d3d3d;

				span { color:#0088dd; }
			}
		}

		a.fade {
            position: absolute;
            text-align: right;
            bottom: -7px;
            left: 15px;
            right: 15px;
            padding-right: 20px;
            padding-top: 53px;
            height: 80px;
            vertical-align: text-bottom;
            color: #9E9E9E;
            font-weight: 900;
            text-transform: uppercase;
            font-size: 14px;
        }
	}

	@media (max-width: 767px) {
		padding: 0 20px 40px 20px;
	}
}

button, button:hover { border-color: transparent; }
ul { margin-left: 0; }
.entry-content ul { padding-left: 1.2em; }

a.more {
	position: relative;
}
a.more:after {
	background: url(../assets/svg/uw-sprite.svg) no-repeat -206px -492px;
	content: "";
	position: absolute;
	right: -24px;
	bottom: 0px;
	width: 14px;
	height: 21px;
	transform: rotate(90deg);
}

body.error404 {
	.uw-hero-image {
		background: url(../assets/headers/suzzallo.jpg) center center no-repeat;
		height: 170px;
		background-size: cover;
		position: relative;
	}

	.woof {
		height:400px;
		background: url(../assets/images/404.jpg) center center no-repeat;
		max-width: 100%;
	}

	h3 {
		margin-bottom: 14.5px;
	}
	p {
		line-height: 1.72222;
	}
}

// Search Widget
#searchform {
	position: relative;
	clear: both;
	outline: 1px solid #ADADAD;
	font-family: $headings;
	font-size: 20px;
	color: #7A7A7A;

	label {
		position: absolute;
		top: 10px;
		left: 18px;
		visibility: hidden;
	}
	.assistive-text {
		  z-index: -1;
	}

	// Field
	#s {
		width: 100%;
		padding: 14px 0 13px 25px;
		border: none;
		font-size: 16px;
		font-weight: 400;
		outline: none;
		background-color: #F5F5F5;
	}

	// Button
	#searchsubmit {
		float: left;
		display: block;
		height: 51px;
		width: 55px;
		text-indent: -99999px;
		outline: 1px solid $purple;
		overflow: hidden;
		background: url(../assets/svg/search.svg) no-repeat center center $purple;
		position: relative;
		border: none;
		border-radius: 0;
		background-size: 22px;
		position: absolute;
		right: 0;
		top: 0;
	}
}

.uw-sidebar .widget_search .widgettitle {
	padding-bottom: 10px;
	&:before,
	&:after {
		display: none;
	}
}
