html {
	overflow-x: hidden;
}

.container-fluid {
    padding-right: 10vw;
    padding-left: 10vw;
}

// Images
.img-horizontal {
	max-width: 720px;
	height: 250px;
	background: url('../img/uw-band.png') no-repeat;
	background-size: cover;
	background-position: center center;
}

.img-thumbnail {
	border: 1px solid #ddd;
    border-radius: 4px;
}

.img-round {
	border-radius: 50%;
}

// figures
.figure-caption {
	position: relative;
    top: -40px;
	left: 15px;

	&.text-right {
		left: auto;
		right: 15px;
	}
}

// tables
.table {
	th {
		border: 0 none;
		color: $purple;
	}

	th[scope=row] {
		border-top: 1px solid #ddd;
	}

	thead th {
		border-bottom: 0 none;
	}

	th, td {
		border-left: 1px solid #ddd;
		border-color: #ddd;
	}

	tr th:first-child {
		border-left: 0 none;
	}
}

.table-striped {
	tbody {
		tr:nth-of-type(odd) {
			background: rgba(0, 0, 0, 0.05);
		}
	}
	th, td {
		border-left: 0 none;
	}
}
