.navbar.bg-primary {
	padding: 0 0 0 1rem;
	margin-bottom: 0;

	@media (max-width: 767px) {
		padding-left: 0;
	}

	@media (min-width: 768px) and (max-width: 991px) {
		flex-flow: column;
		align-items: flex-start;
		margin-top: 20px;
		padding-left: 0;
	}

	.navbar-brand {
		display: inline-flex;
		margin-right: 0;

		@media (min-width: 1045px) and (max-width: 1199px) {
			margin-right: -50px;
		}

		@media (min-width: 992px) and (max-width: 1044px) {
			margin-right: -30px;
		}

		@media (max-width: 767px) {
			margin-right: 0;
		}

		.uw-patch {
			display: inline-flex;
			text-indent: -9999px;
			position: relative;
			bottom: -5px;
			width: 130px;
			height: 90px;
			background: url('../assets/svg/uw-sprite.svg') no-repeat 0 0 transparent;
			z-index: 1;

			&:focus,
			&:hover{
				background: url('../assets/svg/uw-sprite-focus.svg') no-repeat 0 0 transparent;
			}
			&:before {
			//to preload and cache hover svg
				content: url('../assets/svg/uw-sprite-focus.svg');
				display: none;
			}

			@media (max-width: 767px) {
				width: 73px;
				height: 45px;
				left: 5px;
				background-size: 470px;
				transition: all 0s;
				margin-left: 0;

				&:hover,
				&:focus {
					background-size: 470px;
				}
			}
		}
		.uw-wordmark {
			display: inline-flex;
			text-indent: -9999px;
			width: 330px;
			height: 24px;
			position: relative;
			top: 45px;
			left: -30px;
			background: url('../assets/svg/uw-sprite.svg') no-repeat 0 -431px transparent;
			transform: scale(0.8, 0.8);

			@media (max-width: 767px) {
				left: -60px;
				transform: scale(0.6, 0.6);
				top: 15px;
			}

			@media (min-width: 992px) and (max-width: 1105px) {
				left: -40px;
			}

			@media (min-width: 776px) and (max-width: 991px) {
				left: -40px;
				transform: scale(0.8, 0.8);
			}

			@media (min-width: 768px) and (max-width: 851px) {
				margin-right: -50px;
			}

			@media (min-width: 320px) and (max-width: 375px) {
				transform: scale(0.5, 0.5);
				left: -85px;
			}
		}
	}

	.navbar-nav {
		@media (min-width: 992px) and (max-width: 1044px) {
			flex-basis: auto;
		}

		@media (min-width: 769px) and (max-width: 801px) {
			margin-left: -50px;
		}

		@media (min-width: 768px) and (max-width: 991px) {
			order: 2;
			margin-top: 15px;
		}

		@media (max-width: 767px) {
			display: none !important;
		}

		.nav-item {
			.nav-link {
				color: white;
				font-family: $headings, sans-serif;
				font-weight: 700;
				padding-left: 1rem;
				padding-right: 1rem;
				font-size: 18px;
				text-decoration: none;

				&:hover, &:active, &:focus {
					color: $gold;
					text-decoration: underline;
				}
			}
		}
		.nav-item + .nav-item:before {
			content: '';
			display: block;
			position: absolute;
			top: 2.4rem;
			border-left: 1px solid white;
			height: 24px;
			width: 10px;
			transform: skew(-15deg, 0);
		}
	}

	.search-and-quicklinks {
		display: flex;
		flex-wrap: nowrap;
		overflow: hidden;
		position: relative;

		@media (max-width: 767px) {
			display: none;
		}

		@media (min-width: 768px) and (max-width: 991px) {
			align-self: flex-end;
		}

		button {
			border: none;
			display: inline-flex;
			vertical-align: top;
			font-family: $headings;
			font-size: 17px;
			font-weight: $medium;
			position: relative;
			z-index: 1;

			&#search {
				text-indent: -99999px;
				padding-right: 78px;
				border: 2px solid white;
				background-color: transparent;
				border-radius: 100%;
				width: 45px;
				padding: 0;
				height: 45px;
				margin: 0 20px 0 15px;
				overflow: hidden;

				@media (min-width: 992px) and (max-width: 1044px) {
					margin: 0 5px 0 0;
				}

				@media (max-width: 768px) {
					top: 0;
					right: 36px;
					height: 45px;
					width: 47px;
					margin-right: 0;
					border: none;
					overflow: hidden;
					position: absolute;

					&:hover {
						background-color: transparent !important;
						border: none !important;
					}

					svg {
						top: 14px;
						left: 14px;
					}
				}

				&:hover, &:focus {
					border: 2px solid $gold;
					background-color: darken($purple, 5%);
					transition: all .1s linear;
				}
				&:after {
					display: none;
				}
				svg {
					position: absolute;
					top: 12px;
					left: 11px;
				}
			}
			&#quicklinks {
				border-radius: 0;
				align-items: stretch;
				line-height: 1.2;
				transform: skew(-15deg, 0);
				background: $gold;
				height: 45px;
				padding-left: 38px;
				margin-left: -15px;
				margin-right: 0;
				padding-right: 0;
				color: #58595B;
				left: 10px;

				@media (max-width: 767px) {
					// background: transparent;
					// margin-left: -20px;
					top: 0;
					right: 0;
					position: absolute;
				}

				span.ql-label {
					transform: skew(15deg, 0);
					text-transform: none;
					position: relative;
					left: -20px;
					align-self: center;

					@media (max-width: 767px) { display: none}
				}

				span.arrow-box {
					padding: 16px 25px 16px 10px;
					margin-top: -1px;
					border: 0 none;
					background: $darkgold;
					display: flex;
					align-items: center;
					border-left: 2px solid #383838;

					@media (max-width: 767px) {
						background: $gold;
						border-left: 0;
					}

					.arrow {
						border: solid white;
						border-width: 0 2px 2px 0;
						padding: 6px;
						position: relative;
						right: 0;
						transition: right .2s ease;
						transform: skew(15deg, 0) rotate(-45deg);
					}
				}

				&:hover,
				&:focus {
					color: #FFF;
					text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.54);
					text-decoration: underline;
					outline: none;
					transition: all .2s ease;

					span.arrow {
    					right: 5px;
					}
				}

				&:after {
					display: none;
				}
			}
		}
	}
}

.navbar.secondary-nav,
.navbar.white-bar {
	margin-top: 0;
	border: 1px solid rgba(0, 0, 0, 0.14);
	border-top: 0 none;
	background-color: white;

	@media(max-width: 767px) {
		justify-content: flex-start;
		align-items: flex-start;
		// position: absolute;
		// top: 0;
		// right: 0;
		padding: 0;
		background: transparent;
		border: 0 none;
		z-index: 99;
		@include shadow-9;
	}

	.navbar-toggler {
		padding: 0;
		background: white;
		margin: 0.75rem;
		border: 0;
	}

	.navbar-nav {
		flex-flow: wrap;
		@media(max-width: 767px) {
			background: white;
			padding: 0 10px;
			display: flex;
			flex-flow: column;
			margin-top: 5px;

			li { border-bottom: thin solid #eee; }
		}
		.nav-item {
			position: relative;

			a.nav-link, a:visited {
				font-family: $headings, sans-serif;
				text-transform: uppercase;
				font-size: 19px;
				color: $purple;
				font-weight: 800;
				padding-left: 1rem;
				padding-right: 1rem;
				text-decoration: none;

				&.active, &:hover, &:active, &:focus {
					color: $darkgold;
					text-decoration: underline;
				}
			}

			&.current-menu-item a.nav-link {
				color: $darkgold;
			}

			&.dropdown {
				.dropdown-menu {
					padding: 10px 20% 10px;
					border-top: 4px solid $purple;
					border-left: 0 none;
					border-right: 0 none;
					border-bottom: 0 none;
					@include shadow-9;

					@media (max-width: 767px) {
						min-width: 100%;
						padding: 0 20px;
						box-shadow: 0 0 0;
						border-top: 0;
					}

					.multi-column-dropdown {
						list-style-type: none;
						padding: 10px 0;

						li {
							border-bottom: 1px solid #707070;

							a {
								padding: 10px 10px 10px 0;
								display: block;
								text-transform: uppercase;
								color: $black;
								font-family: $bodyfont;
								font-weight: 300;

								&:hover, &:active, &.active {
									color: $gold;
								}
							}
						}
					}

					.dropdown-item {
						font-family: $headings, sans-serif;
						font-weight: 600;
						color: #707070;
						font-size: 18px;
						line-height: 28px;
						border-bottom: thin solid #eee;
						padding: 10px 20px 10px 10px;

						&:last-child {
							border-bottom: 0 none;
						}

						&:hover, &:focus {
							color: $purple;
							background-color:#E7E7E7;
						}
					}
				}
			}
		}

		.nav-item + .nav-item:before {
			content: '';
			display: block;
			position: absolute;
			top: 0.6rem;
			border-left: 1px solid #d9d9d9;
			height: 24px;
			width: 10px;
			transform: skew(-15deg, 0);

			@media (max-width: 767px) {
				display: none;
			}
		}
	}

	&.has-tertiary-nav {
		margin-bottom: 0;
		border: 0 none;
		box-shadow: 0 3px 6px rgba(0, 0, 0, 0.14);
		z-index: 99;
	}
	&.tertiary-nav {
		border: 0 none;
		background-color: #E8E3D3;

		.nav-item + .nav-item:before {
			border-left: 1px solid white;
		}
	}
}
