/*
 *  Styles for the uw youtube player
 *
 */

 .tube-wrapper {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	object {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.nc-video-player {
	clear: both;
	width: 100%;
	background-color: #FFF;
	position: relative;

	iframe {
		max-width: none !important;
		padding: 0;
		margin: 0;
	}
}

.nc-video-player.playlist {
	box-shadow: 1px 1px 7px #E0E0E0;
	margin-bottom: 35px;
	margin-top: 30px;

	&:after {
		content: " ";
		position: absolute;
		bottom: 23px;
		right: 2%;
		width: 5px;
		height: 105px;
	}
}

.playBtn {
	left: 43px;
	position: absolute;
	top: 31px;
	z-index: 0;
	border: none;
}

.vidSmall {
	height: 130px;
	padding: 14px 0 0;
	margin: 0 0 5px 0;
	width: 100%;
	background: #c4c4c4;
	background: -moz-linear-gradient(top, #c4c4c4 0%, #ffffff 6%, #ffffff 100%);
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #c4c4c4), color-stop(6%, #ffffff), color-stop(100%, #ffffff));
	background: -webkit-linear-gradient(top, #c4c4c4 0%, #ffffff 6%, #ffffff 100%);
	background: -o-linear-gradient(top, #c4c4c4 0%, #ffffff 6%, #ffffff 100%);
	background: -ms-linear-gradient(top, #c4c4c4 0%, #ffffff 6%, #ffffff 100%);
	background: linear-gradient(to bottom, #c4c4c4 0%, #ffffff 6%, #ffffff 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#c4c4c4', endColorstr='#ffffff', GradientType=0);

	p {
		display: inline-block;
		line-height: 1.3em;
		font-size: 12px;
		color: white;
	}

	.title {
		margin: 3px 3px 0 0;
	}

	button {
		border: none;
		display: block;
		margin: 0;
		overflow: hidden;
		position: relative;
		padding: 0;
		width: 120px;

		&:hover {
			div {
				opacity: 1;
			}

			.playBtn {
				display: none;
			}
		}

		&:focus {
			div {
				opacity: 1;
			}

			.playBtn {
				display: none;
			}
		}

		div {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			font-weight: 800;
			color: #FFF;
			padding: 5px 10px 5px 10px;
			overflow: hidden;
			background-color: #39275B;
			background-color: rgba(57, 39, 91, 0.9);
			opacity: 0;
			transition: opacity 0.25s ease-in-out;
			-moz-transition: opacity 0.25s ease-in-out;
			-webkit-transition: opacity 0.25s ease-in-out;
		}
	}

	button.vid-active {
		&:after {
			content: "Now Playing";
			padding: 0px 7px;
			background-color: #FFF;
			position: absolute;
			bottom: -3px;
			left: 10px;
			color: #4b2e83;
			text-transform: uppercase;
			font-size: 11px;
			font-weight: 800;
		}

		div {
			opacity: 1;
		}
	}

	img {
		margin: 0;
		padding: 0;
	}

	ul {
		margin: 0px;
		padding: 0px;
	}

	li {
		float: left;
		width: 120px;
		margin: 0 15px 0 0 !important;
		padding: 0;
		background: none;
		list-style: none;
		cursor: pointer;
	}

	.duration {
		color: #B6B6B6;
		background: url('assets/images/youtube-player/play-sm.png') right 2px no-repeat transparent;
		color: #B6B6B6;
		padding-right: 16px;
		float: left;
		margin-top: 3px;
	}

	.viewport {
		height: 95px;
		overflow: hidden;
		position: relative;
		width: 96%;
		margin: 0 2%;
	}

	.overview {
		list-style: none;
		position: absolute;
		left: 0;
		top: 0;
	}

	.thumb {
		.end {
			background-color: #888888;
			overflow: hidden;
			height: 5px;
			width: 8px;
		}
		background-color: #888888;
		height: 7px;
		width: 8px;
		cursor: pointer;
		overflow: hidden;
		position: absolute;
		bottom: 0;
	}

	.scrollbar {
		display: none;
		float: right;
		margin: 0;
		position: absolute;
		bottom: 10px;
		left: 2%;
		height: 7px;
	}

	.track {
		background-color: #f4f4f4;
		height: 100%;
		width: 8px;
		position: relative;
		padding: 0 1px;
	}

	.disable {
		display: none;
	}
}
