/* LinkedIn page specific */
.page-template-page-linkedin #page {
	#page-inner { background-color: #E8E3D3; }
	#primary {
		background-color: white;
		max-width: 50rem;
		margin: 0 auto;
		padding-top: 2em;

		@media (max-width: 768px) {
			margin: 0 10px;
		}
	}

	.entry-header {
		margin: 0 auto 1.5em;

		h1.entry-title {
			text-transform: uppercase;
			color: $purple;
			border-bottom: 1px solid $purple;
			padding-bottom: 0.5em;
			margin: 0 0 0.5em;
			font-size: 45px;
		}
	}

	h2 {
		color: $purple;
		text-transform: uppercase;
		font-weight: bold;
		font-family: $bodyfont;
		font-size: 32px;
		line-height: 1em;
		border-top: 1px solid $purple;
		padding-top: 1em;
	}

	.entry-content {
		padding-bottom: 2em;

		.linkedin-infographic {
			position: relative;

			.linkedin-infographic-img {
				@include shadow-8;
			}
			.linkedin-infographic-cover,
			.linkedin-infographic-profile,
			.linkedin-infographic-about,
			.linkedin-infographic-experience {
				background: $purple;
				color: white;
				padding: 20px 20px 10px 20px;
				position: absolute;
				width: 225px;

				h3 {
					color: $gold;
					text-transform: uppercase;
					font-family: $headingfont;
					font-weight: 600;

					@media (max-width: 768px) {
						font-size: 18px;
					}
				}

				@media (max-width: 468px) {
					p { display: none; }
				}

				@media (max-width: 768px) {
					padding: 10px 10px 5px 10px;
					width: 200px;
				}
			}

			.linkedin-infographic-cover {
				top: 30px;
				right: 30px;

				@media (max-width: 468px) {
					top: 10px;
					right: 35px;
					width: 180px;
				}

				@media (min-width: 469px) and (max-width: 768px) {
					top: 10px;
					right: 35px;
				}
			}

			.linkedin-infographic-profile {
				top: 240px;
				left: 230px;

				@media (max-width: 399px) {
					top: 90px;
					right: 35px;
					left: auto;
					width: 160px;
				}

				@media (min-width: 400px) and (max-width: 468px) {
					top: 90px;
					left: 125px;
					width: 180px;
				}

				@media (min-width: 469px) and (max-width: 500px) {
					top: 160px;
					left: 50px;
				}

				@media (min-width: 501px) and (max-width: 768px) {
					top: 19%;
					left: 32%;
				}
			}

			.linkedin-infographic-about {
				top: 520px;
				left: 250px;
				width: 280px;

				@media (max-width: 399px) {
					top: 180px;
					right: 35px;
					left: auto;
					width: 120px;
				}

				@media (min-width: 400px) and (max-width: 468px) {
					top: 250px;
					right: 60px;
					left: auto;
					width: 120px;
				}

				@media (min-width: 469px) and (max-width: 500px) {
					top: 285px;
					left: initial;
					right: 60px;
					width: 200px;
				}

				@media (min-width: 501px) and (max-width: 768px) {
					top: 35%;
					left: initial;
					right: 60px;
					width: 200px;
				}
			}

			.linkedin-infographic-experience {
				top: 820px;
				right: 70px;
				width: 300px;

				@media (max-width: 399px) {
					top: 320px;
					right: 35px;
					width: 160px;
				}

				@media (min-width: 400px) and (max-width: 468px) {
					top: 440px;
					right: 35px;
					width: 160px;
				}

				@media (min-width: 469px) and (max-width: 500px) {
					top: 460px;
					right: 60px;
					width: 240px;
				}

				@media (min-width: 501px) and (max-width: 768px) {
					top: 52%;
					right: 60px;
					width: 200px;
				}
			}
		}

	}

	.modal-group {
		max-width: 50rem;
		background-color: $purple;
		border-top: 5px solid $gold;
		display: flex;
		justify-content: space-around;
		padding: 10px 0 5px;
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		box-shadow: 0px -8px 10px 0px rgba(0, 0, 0, 0.25);

		// fix for IE11
		@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
			width: 50rem;
			left: initial;
			right: initial;
			margin: 0 auto;
		}

		.btn-social-cats { transition: none; }

		.btn-social-cats {
			color: white;
			text-align: center;
			display: flex;
			flex-flow: column;
			align-items: center;

			&:hover, &:active, &:focus { opacity: 1; border-color: transparent; }

			.btn-social-cats-label {
				font-size: 0.8em;

				@media (max-width: 499px) {
					font-size: 0.6em;
				}
			}

			.btn-social-cats-img {
				width: 57px;
				height: 57px;
				// button pulse
				@media (prefers-reduced-motion: no-preference) {
					animation-name: pulse;
					animation-duration: 1s;
					animation-iteration-count: 5;

					@keyframes pulse {
						0% {
							box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
							border-radius: 50%;
						}
						100% {
							box-shadow: 0 0 0 30px rgba(0, 0, 0, 0);
							border-radius: 50%;
						}
					}
				}
			}

			&.arts .btn-social-cats-img {
				background: url('../images/linkedin-profile/arts-button.png') no-repeat;
			}

			&.arts:hover .btn-social-cats-img,
			&.arts:active .btn-social-cats-img,
			&.arts:focus .btn-social-cats-img {
				background: url('../images/linkedin-profile/arts-button-hover.png') no-repeat;
			}

			&.business .btn-social-cats-img {
				background: url('../images/linkedin-profile/business-button.png') no-repeat;
			}

			&.business:hover .btn-social-cats-img,
			&.business:active .btn-social-cats-img,
			&.business:focus .btn-social-cats-img {
				background: url('../images/linkedin-profile/business-button-hover.png') no-repeat;
			}

			&.communications .btn-social-cats-img {
				background: url('../images/linkedin-profile/communications-button.png') no-repeat;
			}

			&.communications .btn-social-cats-label {
				max-width: 85px;
				word-break: break-word;
			}


			&.communications:hover .btn-social-cats-img,
			&.communications:active .btn-social-cats-img,
			&.communications:focus .btn-social-cats-img {
				background: url('../images/linkedin-profile/communications-button-hover.png') no-repeat;
			}

			&.engineering .btn-social-cats-img {
				background: url('../images/linkedin-profile/engineering-button.png') no-repeat;
			}

			&.engineering:hover .btn-social-cats-img,
			&.engineering:active .btn-social-cats-img,
			&.engineering:focus .btn-social-cats-img {
				background: url('../images/linkedin-profile/engineering-button-hover.png') no-repeat;
			}

			&.research .btn-social-cats-img {
				background: url('../images/linkedin-profile/research-button.png') no-repeat;
			}

			&.research:hover .btn-social-cats-img,
			&.research:active .btn-social-cats-img,
			&.research:focus .btn-social-cats-img {
				background: url('../images/linkedin-profile/research-button-hover.png') no-repeat;
			}
		}
	}

	#arts #cover-expose {
		background: url('../images/linkedin-profile/arts-cover.png') no-repeat;
		background-position: center center;
		background-size: cover;
	}

	#business .modal-header {
		background: url('../images/linkedin-profile/business-cover.png') no-repeat;
		background-position: center center;
		background-size: cover;
	}

	#engineering .modal-header {
		background: url('../images/linkedin-profile/engineering-cover.png') no-repeat;
		background-position: center center;
		background-size: cover;
	}

	#communications .modal-header {
		background: url('../images/linkedin-profile/marketing-cover.png') no-repeat;
		background-position: center center;
		background-size: cover;
	}

	#research .modal-header {
		background: url('../images/linkedin-profile/research-cover.png') no-repeat;
		background-position: center center;
		background-size: cover;
	}

	.modal-content {
		border-radius: 0;

		.modal-header {
			border-top-left-radius: 0;
			border-top-right-radius: 0;
			min-height: 200px;
			transition: border 0.25s linear;
			border: 0 none;
		}

		a.callout-button-1,
		a.callout-button-2,
		a.callout-button-3,
		a.callout-button-4 {
			border: 0 none;
			background: none;
			padding: 0;

			// button pulse
			@media (prefers-reduced-motion: no-preference) {
				animation-name: pulse;
				animation-duration: 1s;
				animation-iteration-count: 5;

				@keyframes pulse {
					0% {
						box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
						border-radius: 50%;
					}
					100% {
						box-shadow: 0 0 0 30px rgba(0, 0, 0, 0);
						border-radius: 50%;
					}
				}
			}

			&:focus {
				outline: 0 none;
			}

			&:hover {
				cursor: pointer;
			}
		}

		a.callout-button-3,
		a.callout-button-4 {
			display: inline-block;
			width: 43px;
			height: 43px;
		}
	}

	.callout-button-1 {
		position: absolute;
		top: -10%;
		left: 25%;

		@media (max-width: 499px) {
			top: -7%;
		}
	}

	.callout-button-2 {
		position: absolute;
		top: 0;
		left: 56%;
	}

	.callout-button-3 {
		position: relative;
		top: -15px;
		left: 10px;
	}

	.callout-button-4 {
		position: relative;
		top: -5px;
		left: 10px;
	}

	.modal-body {
		padding: 0;
	}

	.profile-heading {
		text-align: center;
		padding-bottom: 30px;
		border-bottom: 1px solid #ccc;
		margin: -80px 1rem -20px;

		.profile-pic {
			width: 125px;
			height: 125px;
			border-radius: 50%;
			padding: 0;
			background: white;
			border: 3px solid #ccc;
			display: flex;
			align-items: center;
			margin: 0 auto 20px;
			transition: 0.25s;

			img {
				width: 120px;
				height: 120px;
			}
		}

		h3 {
			font-size: 20px;
			margin-bottom: 0;
		}
	}
	.profile-body {
		padding: 3rem 2rem 2rem;

		h2 {
			border-top: 0 none;
			font-size: 20px;
			color: $black;
			margin-top: 0;
			margin-bottom: 0;
			line-height: 0.8;
		}

		#cover-expose, #about-expose, #experience-expose { transition: 0.25s; }

		.experience-layout {
			display: grid;
			grid-template-columns: 44px 1fr;
			grid-column-gap: 10px;

			img {
				grid-column-start: 1;
				grid-row-start: 1;
			}

			.experience-description {
				grid-column-start: 2;
				grid-row-start: 1;
				transition: 0.25s;

				h3 {
					font-size: 18px;
					margin-bottom: 0;
					font-family: "Open Sans", sans-serif;
					font-weight: 600;
					margin-bottom: 15px;

					&:nth-child(1) {
						margin-bottom: 0;
					}
				}

				p.placeholder-lines {
					border: 10px solid #d1d3d4;
				}
			}
		}
	}

	a:focus { outline: 0; }

	.key-points {
		padding: 0.1rem 2rem 3rem;
		background: $purple;
		color: white;
		margin-bottom: -20px;
		font-weight: lighter;

		h2 {
			border-top: 0 none;
			font-size: 22px;
			color: $gold;
			margin-top: 30px;
		}

		h3 {
			color: white;
			font-size: 18px;
			font-family: $bodyfont, sans-serif;
			font-weight: 600;
		}

		ul {
			margin-left: 1.15em;

			li {
				margin-bottom: 0.8rem;
			}
		}
	}

	.modal-content .modal-header .close {
		color: white !important;
		background: $black;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		margin: 0;
		padding: 0;
		opacity: 1;

		&:not(:disabled):not(.disabled):hover, &:not(:disabled):not(.disabled):focus {
			opacity: 0.75;
		}
	}

	.cover-photo-grid {
		display: grid;
		grid-template-columns: 1fr 1fr;

		@media (max-width: 767px) {
			grid-template-columns: 1fr;
		}
	}
}

.page-template-page-linkedin.modal-open {
	padding-right: 0 !important;
	overflow-y: scroll !important;
}

.modal {
	overflow: auto !important;
}
