/* modals */
.modal-backdrop.show {
	display: none;
}

.modal-dialog {
	top: 7vw;
}

.modal {
	background: rgba(0,0,0,0.5);
}

#social-expose {
	position: relative;
}

#profile-expose {
	position: relative;
}

#about-expose {
	position: relative;
}

#experience-expose {
	position: relative;
}

#social-overlay {
	background: rgba(0,0,0,0.3);
	display: none;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 99998;
}

.social-lightbox {
	background: white;
	cursor: pointer;
	opacity: 1;
	padding: 1rem;
	transition: 0.25s;
}

.page-template-page-linkedin {
	#page {
		.modal-header.social-lightbox {
			border: 10px solid white;
			transition: border 0.25s;
		}

		.profile-heading {
			.profile-pic.social-lightbox {
				width: 150px;
				height: 150px;
			}
		}
	}
}

.popover.show {
	background: var(--purple);

	.arrow {
		display: none !important;
	}
}

.popover.bs-popover-top {
	background: var(--purple);

	.arrow {
		display: none !important;
	}
}

.popover.bs-popover-bottom {
	background: var(--purple);

	.arrow {
		display: none !important;
	}
}

.popover {
	.popover-header {
		background: var(--purple);
		color: var(--gold);
		font-family: var(--bodyfont);
		font-weight: 600;
		border-bottom: 0 none;
		padding: 0;

		&:after {
			content: '\00d7';
			font-size: 1.5rem;
			position: absolute;
			top: 5px;
			right: 10px;
		}
	}

	.popover-body {
		background: var(--purple);
		font-family: var(--bodyfont);
		font-weight: lighter;
		color: white;
		padding: 0;
	}
	padding: 1rem;

	&:hover {
		cursor: pointer;
	}
}

.popover.bs-popover-left {
	background: var(--purple);

	.arrow {
		display: none !important;
	}
}

.bs-popover-bottom {
	.popover-header {

		&:before {
			display: none !important;
		}
	}
	left: -6% !important;
	top: 0% !important;
}

.bs-popover-left {
	.popover-header {
		&:before {
			display: none !important;
		}
	}
	left: 22% !important;
	top: 18% !important;
}

.bs-popover-auto[x-placement^="bottom"] {
	.popover-header {
		&:before {
			display: none !important;
		}
	}
}

.bs-popover-top {
	left: 10% !important;
	top: -3% !important;
}

@media (max-width: 991px) {
	.modal-dialog {
		top: 15vw;
	}
}

@media (min-width: 768px) {
	.modal-group {
		.modal-dialog {
			max-width: 600px;
		}
	}
}

@media (max-width: 499px) {
	.popover.bs-popover-bottom {
		left: -5% !important;
	}

	.popover.bs-popover-top {
		left: 2%;
		top: 0% !important;
	}

	.popover.bs-popover-left {
		left: 0 !important;
	}
}

@media (max-width: 856px) {
	.popover.bs-popover-right {
		left: -10% !important;
		top: 18% !important;
	}
}
