/*
 *  All styles for UW accordion
 */

 .uw-accordion {
	h3 {
		background-color: #E8E8E8;
		color: #565656;
		padding: 15px 15px 15px 15px;
		display: block;
		margin-top: 20px;
		margin-bottom: 20px;
		font-size: 19px;
		cursor: pointer;

		&:after {
			content: " ";
			float: right;
			display: block;
			width: 30px;
			height: 25px;
			background: url('../assets/svg/uw-sprite.svg') no-repeat -283px 2px rgba(255, 255, 255, 0);
		}
	}

	h3.active {
		&:after {
			background-position: -283px -23px;
		}
	}

	div {
		height: 0px;
		overflow: hidden;
	}

	div.active {
		height: auto;
	}
}

.uw-accordion-shortcode__panel[aria-hidden=true] {
	display: none;
}

.uw-accordion-shortcode__header {
	position: relative;
	display: block;
	background: none;
	font-family: inherit;
	font-size: 1.125em;
	text-align: left;
	width: 100%;
	font-weight: bold;
	border: 0;
	padding: 7px 5px;
	border-bottom: 1px solid #DEDEDE;
	color: #4b2e83;
	-o-transition: all 0.1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-webkit-transition: all 0.1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	transition: all 0.1s cubic-bezier(0.25, 0.46, 0.45, 0.94);

	&:hover {
		outline: none;
		background-color: #ddd;
	}

	&:focus {
		outline: none;
		background-color: #ddd;
	}

	&:before {
		content: " ";
		display: block;
		float: right;
		width: 30px;
		height: 25px;
		margin-top: 3px;
		background-image: url('../assets/svg/uw-sprite.svg');
		background-repeat: no-repeat;
	}
}

.uw-accordion-shortcode__panel {
	.uw-accordion-shortcode__header {
		color: #595959;
		padding-left: 20px;
	}
}

.uw-accordion-shortcode__header[aria-expanded="true"] {
	&:before {
		background-position: -283px -23px;
	}
	margin-bottom: 20px;
}

.uw-accordion-shortcode__header[aria-expanded="false"] {
	&:before {
		background-position: -283px 2px;
	}
}

.uw-accordion-shortcode__title {
	height: 1px;
	margin: -1px;
	overflow: hidden;
}
