// buttons

.btn {
	border-radius: 2px;
	padding: 5px 20px;
}
.btn-lg, .btn-group-lg > .btn {
	padding: 0;
	margin: 0.5rem 0;
	font-size: 1rem;
}

a.btn.btn-lg, a.btn.btn-sm {
	text-decoration: none;
}

.btn.btn-lg, .btn.btn-sm {
	border-radius: 0;
	padding-right: 0;
	text-align: left;
	transition: none;
	display: inline-flex;
	align-items: stretch;
	line-height: 1.2;

	&:hover, &:active, &:focus {
		transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	}

	span {
		border: 1px solid;
		text-transform: uppercase;
		font-family: $headings, sans-serif;
		font-weight: 600;
		text-align: center;
		padding: 10px 20px;
	}

	// arrow buttons
	span.arrow-box {
		padding: 10px 18px 10px 10px;
		border: 0 none;
		display: flex;
		align-items: center;

		&.dropdown-box {
			padding: 12px 10px;
		}

		.arrow {
			border-width: 0 1px 1px 0;
			padding: 6px;
			transform: rotate(-45deg);

			&.dropdown-arrow {
				border: solid;
				border-width: 8px 8px 0 8px;
				padding: 0;
				transform: rotate(0);
			}
		}
	}

	&.dropdown-toggle::after { display: none; }

	// square-outline button
	// white button, purple arrow; hovers to gold arrow
	&.square-outline {
		border-radius: 0;
		padding: 0;
		text-align: left;
		transition: none;
		margin: 10px auto;
		display: flex;

		@media (max-width: 500px) {
			padding-left: 1.5rem;
			padding-right: 1.5rem;
			margin-left: auto;
			margin-right: auto;
		}

		span {
			border: 1px solid var(--lightgold);
			text-transform: uppercase;
			color: var(--purple);
			background-color: white;
			font-family: var(--headings), sans-serif;
			font-weight: 600;
			text-align: center;
			line-height: 1.2;
			padding: 10px 20px;
		}

		span.arrow-box {
			background-color: var(--purple);
			padding: 10px 18px 10px 10px;
			border: 0 none;
			display: flex;
			align-items: center;
		}

		span.arrow {
			border: solid var(--lightgold);
			background-color: var(--purple);
			border-width: 0 1px 1px 0;
			padding: 6px;
			transform: rotate(-45deg);
		}

		&:hover {
			border: 1px solid transparent;
			transition: none;

			span.arrow {
				background-color: var(--darkgold);
				border-color: white;
			}

			span.arrow-box {
				background-color: var(--darkgold);
				border-color: white;
			}
		}

		&:active {
			border: 1px solid transparent;
			transition: none;

			span.arrow {
				background-color: var(--darkgold);
				border-color: white;
			}

			span.arrow-box {
				background-color: var(--darkgold);
				border-color: white;
			}
		}

		&:focus {
			box-shadow: 0 0 0;

			span.arrow {
				background-color: var(--darkgold);
				border-color: white;
			}

			span.arrow-box {
				background-color: var(--darkgold);
				border-color: white;
			}
		}
	}

	// purple button, light gold arrow; hovers to gold arrow
	&.purple, &.primary, &.btn-primary {
		span {
			border-color: $purple;
			background-color: $purple;
			color: white;
		}

		&:hover span,
		&:active span {
			border-color: $gold;
			background-color: $gold;
			color: white;
		}

		&:focus {
			box-shadow: 0 0 0 0.2rem rgba(75, 46, 131, 0.5);
			border-color: rgba(75, 46, 131, 0.5);
		}

		&.arrow {
			&:hover span.arrow-box .arrow,
			&:active span.arrow-box .arrow,
			&:hover span.arrow-box,
			&:active span.arrow-box {
				background-color: $gold;
				border-color: white;

				&.dropdown-arrow {
					border-color: $black transparent transparent transparent;
				}
			}

			&:hover span,
			&:active span {
				border-color: $purple;
				background-color: $purple;
				color: white;
			}

			span.arrow-box {
				background-color: $lightgold;

				.arrow {
					border-color: $black;
					background-color: $lightgold;

					&.dropdown-arrow {
						border-color: $black transparent transparent transparent;
					}
				}
			}
		}
	}

	// white button, purple arrow; hovers to gold arrow
	&.white {
		span {
			border-color: $lightgold;
			color: $purple;
			background-color: white;
		}

		&:focus {
			box-shadow: 0 0 0 0.2rem rgba(75, 46, 131, 0.5);
			border-color: rgba(75, 46, 131, 0.5);
		}

		&.arrow {
			&:hover span.arrow-box .arrow,
			&:active span.arrow-box .arrow,
			&:hover span.arrow-box,
			&:active span.arrow-box {
				background-color: $gold;
				border-color: white;

				&.dropdown-arrow {
					border-color: white transparent transparent transparent;
				}
			}

			span.arrow-box {
				background-color: $purple;

				.arrow {
					border-color: white;
					background-color: $purple;

					&.dropdown-arrow {
						border-color: white transparent transparent transparent;
					}
				}
			}
		}
	}

	// light gold button, gold arrow; hovers to black arrow
	&.light-gold, &.secondary {
		&:hover span,
		&:active span {
			border-color: $purple;
			background-color: white;
			color: $purple;
		}

		span {
			border-color: $lightgold;
			background-color: $lightgold;
			color: $purple;
		}

		&:focus {
			box-shadow: 0 0 0 0.2rem rgba(183, 165, 122, 0.5);
			border-color: rgba(183, 165, 122, 0.5);
		}

		&.arrow {
			&:hover span.arrow-box .arrow,
			&:active span.arrow-box .arrow,
			&:hover span.arrow-box,
			&:active span.arrow-box {
				background-color: $black;
				border-color: white;

				&.dropdown-arrow {
					border-color: white transparent transparent transparent;
				}
			}

			span.arrow-box {
				background-color: $gold;

				.arrow {
					border-color: white;
					background-color: $gold;

					&.dropdown-arrow {
						border-color: white transparent transparent transparent;
					}
				}
			}
			&:hover span,
			&:active span {
				border-color: $lightgold;
				background-color: $lightgold;
				color: $purple;
			}
		}
	}

	// gold button, light gold arrow; hovers to purple button
	&.gold {
		&:hover span,
		&:active span {
			border-color: $gold;
			background-color: $gold;
			color: white;
		}

		&:focus {
			box-shadow: 0 0 0 0.2rem rgba(183, 165, 122, 0.5);
			border-color: rgba(183, 165, 122, 0.5);
		}

		span {
			border-color: $gold;
			background-color: $gold;
			color: white;
		}

		&.arrow {
			&:hover span.arrow-box .arrow,
			&:active span.arrow-box .arrow,
			&:hover span.arrow-box,
			&:active span.arrow-box {
				background-color: $purple;
				border-color: white;

				&.dropdown-arrow {
					border-color: white transparent transparent transparent;
				}
			}

			span.arrow-box {
				background-color: $lightgold;

				.arrow {
					border-color: $black;
					background-color: $lightgold;

					&.dropdown-arrow {
						border-color: $black transparent transparent transparent;
					}
				}
			}
			&:hover span,
			&:active span {
				border-color: $gold;
				background-color: $gold;
				color: white;
			}
		}
	}
}

.dropdown-menu.show {
	margin: -2px 0 0 1px;
	border-radius: 0;
}
a.dropdown-item, a.btn {
	text-decoration: none;
}

.show > .btn.btn-lg.arrow.dropdown-toggle.purple {
	span.arrow-box .arrow,
	span.arrow-box {
		background-color: $gold;
	}
}

.show > .btn.btn-lg.arrow.dropdown-toggle.white {
	span {
		border-color: $gold;
		border-bottom-color: white;
		background-color: white;
		color: $purple;
	}

	span.arrow-box .arrow,
	span.arrow-box {
		background-color: $gold;

		&.dropdown-arrow {
			border-color: white transparent transparent transparent;
		}
	}
}

.show > .btn.btn-lg.arrow.dropdown-toggle.gold {
	span {
		border-color: $gold;
		background-color: $gold;
		color: white;
	}

	span.arrow-box .arrow,
	span.arrow-box {
		background-color: $purple;

		&.dropdown-arrow {
			border-color: white transparent transparent transparent;
		}
	}
}

.show > .btn.btn-lg.arrow.dropdown-toggle.light-gold {
	span.arrow-box .arrow,
	span.arrow-box {
		background-color: $black;

		&.dropdown-arrow {
			border-color: white transparent transparent transparent;
		}
	}
}

.btn.btn-sm {
	padding: 0;
	font-size: 0.875rem;
	line-height: 1.5;
	margin: 10px 0;

	span {
		text-transform: none;
		border-radius: 2px;
		padding: 5px 10px;
	}

	&:focus {
		border-radius: 2px;
	}
}

.btn-success, .btn-danger, .btn-warning, .btn-info {
	line-height: 1.2;
	text-transform: uppercase;
	font-family: $headings, sans-serif;
	font-weight: 600;
	text-align: center;
	padding: 10px 20px;
	border-radius: 2px;
}

.link-arrow-box {
	&:hover span.arrow-box .arrow,
	&:active span.arrow-box .arrow,
	&:hover span.arrow-box,
	&:active span.arrow-box {
		background-color: $lightgold;
		border-color: $purple;
	}

	// arrow buttons
	span.arrow-box {
		padding: 7px 9px 6px 4px;
		border: 0 none;
		display: inline-flex;
		align-items: center;
		background-color: $purple;
		margin-left: 5px;

		.arrow {
			border: solid;
			border-width: 0 1px 1px 0;
			padding: 4px;
			transform: rotate(-45deg);
			border-color: white;
			background-color: $purple;
		}
	}
}

// forms
.form-control {
	border-color: #ddd;
	border-radius: 2px;
}

.form-control-lg {
	padding: 1.5rem;
}

label {
	font-family: $headings, sans-serif;
	font-weight: 600;
	font-size: 21px;
}

label.form-check-label {
	font-family: $bodyfont;
	font-weight: normal;
	font-size: 1rem;
}

.form-control:disabled, .form-control[readonly] {
	border-color: #818A91;
	background-color: #ddd;
	color: $black;
}

.input-group-text {
	border-radius: 2px;
	border: 1px solid #ddd;
	background: #ECEEEF;
}

.custom-file-label:after {
	background-color: $purple;
	color: white;
	border-radius: 0 2px 2px 0;
}

.invalid-feedback { margin-top: 0.5rem; }
.custom-control-label:before { top: 0.5rem; }
.custom-select, .custom-file-input { border-radius: 2px; }
.custom-control-label:after { top: 0.5rem; }
.custom-checkbox .custom-control-label:before { border-radius: 2px; }

// search bar (in-page)
.input-group.search {
	align-items: flex-start;
	width: 30%; // will need to tweak this depending on space it's in

	input[type='search'] {
		border: 2px solid #ddd;
		background: #f5f5f5 padding-box;
	}
	.input-group-append {
		button {
			padding: 12px;

			span {
				border: 1px solid white;
				border-radius: 50%;
				padding: 3px;

				svg {
					position: relative;
					top: 2px;
					left: 4px;
				}
			}
		}
	}
}

// cards
.card-grid {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	align-items: start;
}
.card {
	background-color: $lightgold;
	border: 0 none;
	border-radius: 2px;

	.card-body {
		padding: 2rem;

		p {
			font-family: $bodyfont, sans-serif;
			font-weight: 300;
		}

		h2, h3, h4 {
			color: $purple;
			font-family: $headings, sans-serif;
			font-size: 2rem;
			font-weight: 900;
			margin-bottom: 1rem;
		}

		.card-title-slant { margin-bottom: 0; }

		.card-img {
			margin-bottom: 2rem;
			border-radius: 2px;

			&.card-img-inset {
				width: auto;
				margin-top: 1rem;
			}
		}

		.btn-sm.btn-primary {
			border-radius: 2px;
		}

		.btn-sm.btn-primary:hover,
		.btn-sm.btn-primary:active {
			border-color: $gold;
			background-color: $gold;
			border-radius: 2px;
		}
	}
	&.white.purple-top {
		.card-title {
			font-family: $headings, sans-serif;
			font-size: 2rem;
			font-weight: 700;
			background-color: $purple;
			color: white;
			padding: 1.35rem 2rem;
			margin-bottom: 0;
			border-top-left-radius: 2px;
			border-top-right-radius: 2px;
		}

		.card-body {
			background-color: white;
			border: 1px solid rgba(112, 112, 112, 0.2);
			border-bottom-left-radius: 2px;
			border-bottom-right-radius: 2px;
		}
	}

	&.white.text-button {
		.card-body {
			background-color: white;
			border: 1px solid rgba(112, 112, 112, 0.2);
			border-radius: 2px;
		}

	}

	&.large, &.full-width {
		width: 100%;
		margin: 20px auto;
		border: 1px solid rgba(112, 112, 112, 0.2);
		@include shadow-3;
		display: grid;
		grid-template-columns: 1fr 1fr;
		overflow: hidden;

		&.white {
			background-color: white;

			h2, h3, h4 {
				color: $black;
			}

			p {
				color: $black;
			}

			.card-body {
				background-color: white;
			}
		}

		&.purple {
			background-color: $purple;
			h2, h3, h4 {
				color: white;
			}

			p {
				color: white;
			}

			.card-body {
				background-color: $purple;
			}
		}

		&.lightgold {
			background-color: $lightgold;

			h2, h3, h4 {
				color: $black;
			}

			p {
				color: $purple;
			}

			.card-body {
				background-color: $lightgold;
			}
		}

		.image-large {
			width: 100%;
			height: auto;
			background: url('../img/uw-band.png') no-repeat;
			background-size: cover;
			background-position: center center;
		}

		.card-body {
			padding-left: 15px;
			padding-right: 0;
			transform: skew(-15deg, 0);
			position: relative;
			left: -3.5rem;

			@media (min-width: 768px) and (max-width: 991px) {
				left: -5rem;
			}

			@media (min-width: 992px) and (max-width: 1199px) {
				left: -3.75rem;
			}
		}

		.inner-card-body {
			margin-left: 5rem;
			padding-right: 1rem;
			transform: skew(15deg, 0);
			max-width: 75%;

			@media (max-width: 767px) {
				max-width: 100%;
			}

			@media (min-width: 768px) and (max-width: 991px) {
				max-width: 85%;
			}
		}

		@media (max-width: 767px) {
			.card-body, .inner-card-body { transform: none; }
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 1fr;
		}
	}

	&.full-width {
		width: 100vw;
		position: relative;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
		border: 0 none !important;
		box-shadow: 0 0 0 !important;
		border-radius: 0;

		.card-body {
			padding: 9rem 0;
			left: -4rem;

			@media (max-width: 767px) {
				padding: 2rem 3rem 6rem;
			}

			@media (max-width: 991px) and (min-width: 768px) {
				left: -5rem;
			}

			@media (min-width: 992px) and (max-width: 1199px) {
				left: -4.5rem;
			}
		}

		&.img-right {
			.card-body {
				left: 5rem;

				@media (max-width: 767px) {
					left: -4rem;
					padding: 3rem;
				}
			}
		}
	}
}

// UW Slant
.udub-slant-divider {
	@include udub-slant-divider;
}

// UW Slant image overlay pattern
svg.slant-pattern {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 90px;

	rect.hbar {
		mask: url(#mask-stripe);
	}

	.purple-lines {
		fill: rgba(75, 46, 131, 0.5); // $purple rgb+a
	}

	.white-lines {
		fill: rgba(255, 255, 255, 0.5);
	}
}

// jumbotrons
.jumbotron.img-background {
	background: url('../img/new-burke-aerial.png') no-repeat;
	background-size: cover;
	background-position: center center;
	position: relative;
	padding: 6rem;

	.display-3, p {
		background: $purple;
		color: white;
		padding: 1rem;
	}

	.display-3 {
		font-family: $headings, sans-serif;
		font-weight: 900;
	}

	p {
		font-weight: 300;
		margin-bottom: 0;
	}

	p.text-dark { font-weight: normal; }

	svg.slant-pattern {
		height: 35px;
	}

	.d-flex-column.w-40 {
		text-align: center;
		margin: 0 auto;

		@media (min-width: 768px) and (max-width: 989px) {
			width: 60% !important;
		}

		.container {
			background: $purple;
			padding: 20px 40px;

			.display-3 {
				padding-bottom: 0;
				margin-bottom: 0; }
		}
	}

	@media (max-width: 767px) {
		.w-40 { width: 100% !important; }
	}

	&.overlay {
		padding: 6rem 0;
		overflow: hidden;

		.transparent-overlay-slant {
			background-color: rgba(85, 52, 151, 0.9); // $purple rgb+a
			color: white;
			max-width: 45%;
			padding-left: 7rem;
			transform: skew(-15deg, 0);
			position: relative;
			left: -3.5rem;

			@media (max-width: 767px) {
				max-width: 90%;
			}

			.inner-overlay {
				transform: skew(15deg, 0);
				padding: 3rem 5rem 2rem 3rem;

			}
			.display-3, p {
				background: none;
				max-width: 100%;
				padding: 0;
			}
		}
	}
	&.no-back {
		background-position: top center;

		.display-3, p {
			background: none;
			padding: 0;
		}
	}
	&.full-width {
		width: 100vw;
		position: relative;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
	}
}

// navigation
.sidebar .widget {
	width: 40%;
	max-width: 100%;
	h4 {
		color: white;
		background: $purple;
		padding: 10px 20px;
		margin-bottom: 0;
		border-right: 50px solid $gold;
	}
	.nav.flex-column {
		li {
			border-bottom: 1px solid white;

			&.has-children span { display: block; }

			a, &.has-children span {
				background: #eee;
				color: $black;
				text-decoration: none;
				border-left: 10px solid transparent;
			}

			a.active, a:hover, a:active, a:focus, &.has-children.active span {
				background: #ddd;
				border-left-color: $gold;
			}

			&.has-children.active  {
				ul.children {
					margin-left: 0;
					padding-left: 0;
					list-style-type: none;
					background: white;
					li {
						a {
							background: white;

							&:before {
								content: '- ';
							}
							&:hover, &:active {
								border-left-color: transparent;
							}
						}
					}
				}
			}
		}
	}
}

nav.popular-links {
	@include shadow-2;
	border: 1px solid rgba(0, 0, 0, 0.14);
	border-radius: 2px;

	ul.nav {
		display: grid;
        grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
		justify-content: center;
		margin: 0;
		padding: 0;

		li {
			flex: 1 1 auto;
			border-left: 1px solid rgba(0, 0, 0, 0.14);
			line-height: 20px;
			margin: 40px 0;

			&:first-child { border-left: 0 none;  }

			a {
				display: flex;
				flex-direction: column;
				align-items: center;
				text-decoration: none;

				span, h4 {
					display: block;
					text-align: center;
				}

				h4 {
					color: $purple;
					font-family: $headings, sans-serif;
					font-weight: bold;
					font-size: 20px;
					margin-top: 10px;
				}

				span {
					color: $black;
					text-decoration: underline;
				}
			}
		}
	}

	&.boxes {
		box-shadow: 0 0 0;
		border: 0 none;

		ul.nav  {
			justify-content: space-evenly;
			grid-gap: 20px;

			li {
				flex: none;

				@include shadow-2;
				border: 0 none;
				border: 1px solid rgba(0, 0, 0, 0.14);
				border-radius: 2px;
				margin-bottom: 20px;
				padding: 40px 20px;
			}
		}
	}
}

#nav-basic, #hover-nav, #double-nav {
	position: relative;
}

.carousel {
	&.full-width {
		width: 100vw;
		position: relative;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
		border: 0 none !important;
		box-shadow: 0 0 0 !important;
		border-radius: 0;

		.carousel-inner {
			.carousel-caption {
				background: $purple;
				text-align: left;
				bottom: 0;

				.display-3, p {
					background: $purple;
					color: white;
					padding: 0 2rem;
				}

				.display-3 {
					font-family: $headings, sans-serif;
					font-weight: 900;
					font-size: 30px;
				}

				p {
					font-weight: 300;
					margin-bottom: 0;
					padding-top: 1rem;
				}

				.btn {
					padding-left: 2rem;
					padding-top: 1rem;
				}
			}

		}
		.carousel-control-next, .carousel-control-prev {
			width: 6rem;

			&:hover {
				background: rgba(75, 46, 131, 0.8);
			}

			.carousel-control-next-icon, .carousel-control-prev-icon {
				background: none;
				border: solid;
				padding: 6px;
				position: relative;
				transition: left .2s ease;
			}

			.carousel-control-next-icon {
				border-width: 0 4px 4px 0;
				transform: rotate(-45deg);
			}

			.carousel-control-prev-icon {
				border-width: 0 0 4px 4px;
				transform: rotate(45deg);
			}
		}
	}

	&.homepage {
		.carousel-inner {
			.carousel-caption {
				right: inherit;
				bottom: inherit;
				top: 3rem;
				left: 6rem;
				text-align: left;
				background: none;

				.display-3, p {
					padding: 1rem;
				}

				.display-3 { font-size: 50px; }

				.btn {
					padding-left: 0;
				}
			}

			svg.slant-pattern {
				height: 35px;
			}
		}

		.carousel-control-next {
			top: inherit;
			bottom: 1rem;
			right: 15rem;
			opacity: 1;
			background: none;
			transition: none;
			text-decoration: none;

			span {
				font-family: $headings, sans-serif;
				text-transform: uppercase;
				font-size: 18px;
				font-weight: bold;
				text-align: left;
				line-height: 1.2;
				text-shadow: rgba(0, 0, 0, 0.5) 3px 3px 6px;
			}

			.carousel-control-next-icon {
				left: -20px;
			}

			&:before {
				content: '';
				display: block;
				position: relative;
				top: 1rem;
				border-left: 2px solid white;
				height: 120px;
				width: 38px;
				transform: skew(-15deg, 0);
			}

			&:hover, &:active, &:focus {
				.carousel-control-next-icon {
					left: -15px;
				}
				span {
					text-shadow: rgba(0, 0, 0, 0.8) 3px 3px 6px;
				}
			}
		}

		.carousel-control-next, .carousel-control-prev {
			width: auto;

			&:hover, &:active, &:focus {
				background: none;
			}
		}
	}
}

.bs-popover-right > .arrow::after,
.bs-popover-auto[x-placement^="right"] > .arrow::after {
	border-right-color: $purple;
}

.bs-popover-top > .arrow::after,
.bs-popover-auto[x-placement^="top"] > .arrow::after {
	border-top-color: $purple;
}

.bs-popover-bottom > .arrow::after,
.bs-popover-auto[x-placement^="bottom"] > .arrow::after {
	border-bottom-color: $purple;
}

.bs-popover-left > .arrow::after,
.bs-popover-auto[x-placement^="left"] > .arrow::after {
	border-left-color: $purple;
}

.popover-header { border-bottom: none; }
.popover { font-family: 'Open Sans', sans-serif; }

#popovers {
	.btn.btn-lg, .btn-group-lg > .btn, .btn.btn-sm, .btn-group-sm > .btn {
		padding-right: 20px;
	}
}

.badge {
	border-radius: 0;
}

// pagination
.pagination {
	.nav-links {
		display: flex;
		padding-left: 0;
		list-style: none;
		border-radius: 0.25rem;

		.page-numbers {
			position: relative;
			display: block;
			padding: 0.5rem 0.75rem;
			margin-left: -1px;
			line-height: 1.25;
			color: $links;
			background-color: white;
			border: 1px solid #dee2e6;

			&:hover {
				z-index: 2;
				color: $darkgold;
				text-decoration: none;
				background-color: #e9ecef;
				border-color: #dee2e6;
			}

			&:focus {
				z-index: 2;
				outline: 0;
				box-shadow: 0 0 0 0.2rem rgba(75, 46, 131, 0.25);
			}

			&:first-child {
				margin-left: 0;
				border-top-left-radius: 0.25rem;
				border-bottom-left-radius: 0.25rem;
			}
			&:last-child {
				border-top-right-radius: 0.25rem;
				border-bottom-right-radius: 0.25rem;
			}

			&.current {
				z-index: 1;
				color: white;
				background-color: $purple;
				border-color: $purple;
			}
		}
	}
}
